import Vue from 'vue'
import Router from 'vue-router'
import paths from './paths'

function route (path,file,name,view){
  let fileName = `/${file}`;
  if(!file){ fileName = `` };
  return {
    name: name || file,
    path,
    component: (resovle) => import(
        `../components${fileName}/${view}.vue`
        ).then(resovle)
  }
}

Vue.use(Router)

export default new Router({
  mode:'hash',
  routes:paths.map(path=>route(path.path,path.file,path.name,path.view,path.alone))
})
