<template>
    <div>
        <!--=== LoginModel ===-->
        <div class="modal fade" id="loginModel" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalCenterTitle">Log in</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <div class="modal-body">
                        <form>
                            <div class="form-group">
                                <label for="inputPhone">UserName</label>
                                <input type="email" class="form-control" id="inputPhone" aria-describedby="emailHelp" placeholder="UserName" v-model="loginData.username">
                                <small id="emailHelp" class="form-text text-muted pl-2">Please enter the Username</small>
                            </div>
                            <div class="form-group">
                                <label for="inputPassword">Password</label>
                                <input type="password" class="form-control" id="inputPassword" placeholder="Password" v-model="loginData.password">
                            </div>
                            <div class="form-check">
                                <input type="checkbox" class="form-check-input" id="remember" value="1" name="remember">
                                <label class="form-check-label" for="remember">Remember me on this computer</label>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer justify-content-end">
                        <button type="button" class="btn btn-secondary"  data-toggle="modal" data-target="#registerModel" data-dismiss="modal" >Register</button>
                        <button type="button" class="btn btn-primary ml-2" id="sentLogin" @click="login">Log in</button>
                    </div>
                </div>
            </div>
        </div>

        <!--== register ==-->
        <div class="modal fade" id="registerModel" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalCenterTitle">Register</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
