                    </div>
                    <div class="modal-body">
                        <form>
                            <div class="form-group">
                                <label for="username">User Name <span class="text-danger">*</span></label>
                                <input type="email" id="username" class="form-control" aria-describedby="emailHelp" placeholder="please enter user name" v-model="registerData.username">
                            </div>
<!--                            <div class="form-group">-->
<!--                                <label for="nick">Nike Name</label>-->
<!--                                <input type="email" id="nick" class="form-control " aria-describedby="emailHelp" placeholder="Please enter your nickname" v-model="registerData.nick">-->
<!--                                <div class="invalid-feedback">-->
<!--                                    Please provide a valid city.-->
<!--                                </div>-->
<!--                            </div>-->
<!--                            <div class="form-group">-->
<!--                                <label for="" >性别<span class="text-danger">*</span></label>-->
<!--                                <div class="form-check form-check-inline ml-3">-->
<!--                                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio1" value="1">-->
<!--                                    <label class="form-check-label" for="inlineRadio1">男</label>-->
<!--                                </div>-->
<!--                                <div class="form-check form-check-inline">-->
<!--                                    <input class="form-check-input" type="radio" name="inlineRadioOptions" id="inlineRadio2" value="0">-->
<!--                                    <label class="form-check-label" for="inlineRadio2">女</label>-->
<!--                                </div>-->
<!--                            </div>-->
                            <div class="form-group">
                                <label for="Password">Password <span class="text-danger">*</span></label>
                                <input type="password" class="form-control" id="Password" placeholder="please enter your password" v-model="registerData.password">
                            </div>
                            <div class="form-group">
                                <label for="checkPassWord">Password again <span class="text-danger">*</span></label>
                                <input type="password" class="form-control" id="checkPassWord" placeholder="please enter your password again" v-model="registerData.checkPassoWrod">
                            </div>
                            <div class="form-group">
                                <label for="phone">E-mail <span class="text-danger">*</span></label>
                                <input type="email" class="form-control" id="phone" aria-describedby="emailHelp" placeholder="Please enter your email address" v-model="registerData.mobile">
                                <div class="invalid-feedback">
                                    Please check if it is correct
                                </div>
                            </div>
<!--                            <div class="form-group">-->
<!--                                <label for="yzm">验证码 <span class="text-danger">*</span> </label>-->
<!--                                <div class="input-group ">-->
