<!--                                </div>-->
<!--                            </div>-->
                        </form>
                    </div>
                    <div class="modal-footer justify-content-end">
                        <button type="button" class="btn btn-secondary  bg-green"  @click="register">Register</button>
                        <button type="button" class="btn btn-link" data-toggle="modal" data-target="#loginModel" data-dismiss="modal" > Already registered ? Click to Login</button>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    import { mapActions } from 'vuex';
    export default {
        name: "login",
        data:()=>({
            registerData:{
                nick:''
            },
            registerFlage:false,
            count:0,
            loginData:{}
        }),
        watch:{
            count(){
                if(this.count === 0){
                    this.count = 0
                    this.registerFlage = false;
                }else{
                    setTimeout(()=>{
                        this.count--;
                    },1000)
                }
            }
        },
        methods:{
            ...mapActions(['checkUser']),
            register(){
                let _self = this;
                if(!_self.registerData.username){
                    _self.$message({
                        message: 'please enter user name',
                        type:'warning'
                    })
                    return;
                }
                //
                // if(!_self.registerData.nick){
                //     _self.$message({
                //         message: 'Please enter your nickname',
                //         type:'warning'
                //     })
                //     return;
                // }
                //
                // _self.registerData.sex = $('input[name="exampleCheck1"]:checked').val();
                //
                // if(!_self.registerData.sex){
                //     _self.$message({
                //         message: '请选择性别',
                //         type: 'warning'
                //     });
                //     return
                // }
                //
                if(!_self.registerData.password){
                    _self.$message({
                        message: 'please enter your password',
                        type: 'warning'
                    });
                    return;
                }
                //
                if(!_self.registerData.checkPassoWrod){
                    _self.$message({
                        message: 'please enter your password again',
                        type: 'warning'
                    });
                    return;
                }
                //
                if(_self.registerData.checkPassoWrod != _self.registerData.password){
                    _self.$message({
                        message: 'The password entered twice is different\n',
                        type: 'warning'
                    });
                    return;
                }
                //
                if(!_self.registerData.mobile){
                    _self.$message({
                        message: 'Please enter your email address',
                        type: 'warning'
                    });
                    return;
                }
                //
                // if(!_self.registerData.msg){
                //     $('#yzm').addClass('is-invalid');
                //     setTimeout(()=>{
                //         $('#yzm').removeClass('is-invalid');
                //     },3000)
                //     return
                // }
                //

                _self.$ajax.post('/user/register',_self.registerData).then(res=>{
                    if(res.resultCode/1 === 1){
                        _self.$message({
                            message: res.resultMsg,
                            type: 'success'
                        });
                        _self.registerData = {};
                        $('#registerModel').modal('hide');
                    }
                })
                //
            },
            // yzm(){
            //     let _self = this;
            //     if(_self.registerFlage){
            //         _self.$message({
            //             message: '冷却中，请稍后再试'+`${count}秒`,
            //             type: 'success'
            //         });
            //     }
            //     if(!_self.registerData.mobile){$('#phone').addClass('is-invalid');
            //         setTimeout(()=>{
            //             $('#phone').removeClass('is-invalid');
            //         },3000);
            //         return;
            //     }
            //     if(_self.registerData['mobile'].length < 11 ||_self.registerData['mobile'].length > 11){$('#phone').addClass('is-invalid');
            //         setTimeout(()=>{
            //             $('#phone').removeClass('is-invalid');
            //         },3000);
            //         return;
            //     }
            //     _self.registerFlage = true;
            //     _self.count = 30;
            //     //
            //     let data = {
            //         mobile:_self.registerData.mobile
            //     };
            //     //
            //     _self.$ajax.post('/user/send_code',data)
            //         .then(res=>{
            //             // console.log(res)
            //             if(res.resultCode/1 === 1){
            //                 _self.$message({
            //                     message: '短息发送成功',
            //                     type: 'success'
            //                 });
            //                 setTimeout(()=>{
            //                     _self.registerFlage = false;
            //                 },30000)
            //             }else{
            //                 _self.$message({
            //                     message: '短信发送失败',
            //                     type: 'error'
            //                 });
            //                 _self.registerFlage = false;
            //             }
            //         })
            // },
            login(){
                let _self = this;

                if(!_self.loginData.username){
                    _self.$message({
                        message: 'Please enter your username',
                        type: 'error'
                    });
                    return;
                }
                if(!_self.loginData.password){
                    _self.$message({
                        message: 'Please enter your password',
                        type: 'error'
                    });
                    return;
                }
                let remember = $('input[name="remember"]:checked').val();
                _self.$ajax.post('/user/login',_self.loginData)
                    .then(res=>{
                        console.log('login',res)
                        if(res.resultCode === 0){
                            _self.$message({
                                message: res.resultMsg,
                                type: 'error'
                            });
                        };
                        if(res.resultCode === 1){
                            if(remember){
                                window.localStorage.setItem("_REMEMDER_ME_", JSON.stringify(res.resultData));
                                _self.checkUser();
                                _self.loginData = {}
                                $('#loginModel').modal('hide');
                            }else{
                                _self.checkUser(res.resultData);
                                $('#loginModel').modal('hide');
                            }
                        }
                    })
            },
        },//methods
    }
</script>

