import Vue from 'vue'
import App from './App.vue'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.min'
import router from './router/router'
import store from './store'
import './css/ionicons.css'
import './css/now-ui-kit.css'
import './css/app.css'
import axios from "./utils/axios"
import './utils/utils'
import {Button, Upload, Dialog, Message, Loading, Popover } from 'element-ui';

Vue.prototype.$ajax = axios;
Vue.prototype.$message = Message;
Vue.config.productionTip = false;

Vue.use(Button);
Vue.use(Upload);
Vue.use(Dialog);
Vue.use(Popover);
Vue.use(Loading.directive);
Vue.prototype.$loading = Loading.service;
//混入图片地址
Vue.mixin({
  data(){
    return {
      imgUrl: 'http://vacuumcup.laoma-app.com',
      load:null,
    }
  },
  methods:{
    loading(){
      this.load = this.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
    },
    closeLoading(){
      this.load.close()
    }
  }
});

(function (){
  axios.post('/common/token',{})
        .then(res=>{
          window.localStorage.setItem('USER_TOKEN_A',res.token);
          new Vue({
            router,
            store,
            render: h => h(App)
          }).$mount('#app');
        });
})();

