import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        userInfo: false,
    },
    mutations: {
        up_userInfo(state, data) {
            state.userInfo = data
        },
    },
    actions: {
        checkUser({commit},data) {
            let info = window.localStorage.getItem('_REMEMDER_ME_');
            if (!!info) {
                commit('up_userInfo', JSON.parse(info))
            }else{
                commit('up_userInfo', data)
            }
        }
    },
    getters: {
        userInfo: state => state.userInfo
    }
})
