import Vue from 'vue'
import moment from 'moment'

/* 字符串裁剪 */
Vue.filter('size', function slicesStr(dataStr, num = 20) {
    if (dataStr) {
        if (dataStr.length >= num) {
            return dataStr.slice(0, num) + "..."
        } else {
            return dataStr
        }
    } else {
        return dataStr
    }
});

/*全局时间处理*/
Vue.filter('time', function(val, format = 'YYYY-MM-DD HH:ss:mm') {
    let time = val * 1000;
    return moment(time).format(format)
});




